$(".to-menu").click(function() {
    $('html, body').animate({
        scrollTop: parseInt($("#menu").offset().top - 210)
    }, 1000);
});
$(".to-offers").click(function() {
    $('html, body').animate({
        scrollTop: parseInt($("#offers").offset().top - 150)
    }, 1000);
});
$(document).ready(function() {})