import * as slick from 'slick-carousel';


$(document).ready(function() {
    const heroSlider = () => {
        let slider = $('.hero .slider');
        slider.slick({
            arrows: false,
            dots: true,
            autoplay: true,
            autoplaySpeed: 10000,
        })
    }
    heroSlider()

    const menuSlider = (slideNumber) => {
        let sliderMenu = $('.menu__slider');
        sliderMenu.slick({
            dots: false,
            infinite: true,
            speed: 300,
            slidesToShow: slideNumber,
            infinite: false,
            autoplay: true,
            autoplaySpeed: 3000,

        })
    }
    menuSlider(11)


    const offersSlider = () => {
        let slider = $('.offers__slider');
        slider.slick({
            dots: true,
            arrows: false,
            infinite: true,
            slidesToShow: 3,
            speed: 300,
            autoplay: true,
            autoplaySpeed: 3000,
            responsive: [{
                breakpoint: 1024,
                settings: {
                    slidesToShow: 2,

                }
            }]

        })
    }
    offersSlider()

    const destroySlider = () => {
        let sliderMenu = $('.menu__slider');
        sliderMenu.addClass('menu__row')
        sliderMenu.removeClass('menu__slider')
        sliderMenu.slick('unslick')
    }

    if (window.innerWidth <= 1024) {
        destroySlider()
    } else if (window.innerWidth >= 1024) {
        let sliderMenu = $('.menu__row');
        sliderMenu.addClass('menu__slider');
        sliderMenu.removeClass('menu__row');
    }
    $(window).on('resize', function() {
        if (window.innerWidth <= 1024) {
            let sliderMenu = $('.menu__slider');
            destroySlider();
            sliderMenu.removeClass('menu__slider');
        } else {
            let sliderMenu = $('.menu__row');
            sliderMenu.addClass('menu__slider');
            sliderMenu.removeClass('menu__row');
            menuSlider(11)
        }
    })
})